.container-login{
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 15px;
    background-color: #b7d5e5;
}


.login {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    background-color: #fff;
    color: white;
    border-radius: 30px;
}

.tslogo img{
    max-width: 400px;
    max-height: 400px;
    width: auto;
    height: auto;
}

.card-login{
    text-align: center;
    padding-right: 10px;
}
.card-login h1{
    font-family: Poppins-Bold;
    font-size: 40 px;
    color: #333333;
    padding-bottom: 20px;
}

.loginInputEmail{
    display: flex;
    align-items: center;
    color: white;
    width: 98%;
    border-radius: 5px;
    height: 40px;
    text-align: center;
    padding-left: 20px;
    
}
.loginInputPassword{
    display: flex;
    align-items: center;
    color: white;
    width: 98%;
    border-radius: 5px;
    height: 60px;
    text-align: center;
    padding-left: 20px;
     
}

.loginInputEmail svg{
    margin-right: 4px;
    color: #1d1e20;
}
.loginInputPassword svg{
    margin-right: 4px;
    color: #1d1e20;
}
.loginInputEmail input{
    font-family: Poppins-Medium;
    font-size: 15px;
    line-height: 1.5;
    color: #666666;
    display: block;
    width: 47%;
    background: #e6e6e6;
    height: 35px;
    border-radius: 25px;
    padding: 0 30px 0 40px;
    border: none;
}
.loginInputPassword input{
    font-family: Poppins-Medium;
    font-size: 15px;
    line-height: 1.5;
    color: #666666;
    display: block;
    width: 47%;
    background: #e6e6e6;
    height: 35px;
    border-radius: 25px;
    padding: 0 30px 0 40px;
    border: none;
}
.card-login button{
    width: 30%;
    background-color: #1E90FF;
    color: #e6e6e6;
    font-weight: 800;
    height: 30px;
    border-radius: 6px;
    border: none;
    margin-top: 5px;
    outline-width: 0;
}
.card-login button:hover{
  background-color: #539be2
}
.login-eye {
    align-items: center;
    justify-content: center;
    font-size: 30;
    cursor: pointer;
    padding-left: 5px;
}